img {
    width: 100%;
    height: auto;
}
.profileInfoContainer {
    display: flex;
}
.contacts {
    flex-grow: 2;
}
.profileInfo ul li{
list-style: none;
}

.addPhotoButtonContainer {
    position: absolute;
    top: 0px;
    width: 100%;
    background-color: rgba(180, 158, 240, 1);
    padding: 5px 0px 5px 0px;
    display: none;
}
.addPhotoButtonContainer input {
    color: white;
    margin-left: 5px;
}
.profilePictureContainer {
    position: relative;
    width: 200px;
    height: 200px;
    cursor: pointer;
    overflow: hidden;
}
.loadingPhotoContainer {
   position: absolute;
   top: 0px;
   width: 100%;
   height: 100%;
   display: flex;
   justify-content: center;
   align-items: center;
   background-color: rgba(81, 44, 130, 0.5);
   color: white;
   font-size: 18px;
}
.hideLoading {
    display: none;
}